import React from 'react'
import Box from './Box'
import { responsive } from './ThemeProvider/theme'

const Source = ({ borderColor, children, note, ...props }) => {
  return (
    <Box
      mt="1.875rem"
      pt="1rem"
      pb="5em"
      fontSize={responsive('0.5em', '1em')}
      borderTop="1px solid"
      borderColor={borderColor}
      {...props}
    >
      {!note && '資料來源：'}{children}
    </Box>
  )
}

export default Source
