import React from 'react'
import Box from './Box'
import Text from './Text'
import { responsive } from './ThemeProvider/theme'

const UpCircle = ({ img, show, title, text }) => {
  return (
    <Box
      width="100%"
      position="relative"
      transform={responsive(show ? 'translateY(0%)' : 'translateY(-100%)', null, show ? 'translateX(-5%)' : 'translateX(-100%)')}
      transition="all 0.5s"
    >
      {img}
      <Box.AbsCenter
        textAlign="center"
        color="white"
        left={responsive('50%', null, '12.5%')}
        transform={responsive('translate(-50%, -75%)', null, 'translate(0%, -52.5%)')}
      >
        <Text fontSize={responsive('1.25em', null, '2em')} whiteSpace="pre-wrap">{title}</Text>
        <Box.Inline
          fontSize={responsive('1.125em', null, '1.5em')}
          pt="0.375rem"
          mt="0.375rem"
          borderTop="1px solid white"
        >{text}</Box.Inline>
      </Box.AbsCenter>
    </Box>
  )
}

export default UpCircle
